import React from 'react';
import ReactDOM from 'react-dom';
import './App.css'

function InputName() {
  return (
    <label>
      <input
        id='username'
        name='name'
        type='text'
        pattern='[a-zA-Z,а-яёА-ЯЁ]{3,20}\s[a-zA-Z,а-яёА-ЯЁ]{3,20}'//type check here
        title="*Имя пользователя должно содержать только строчные буквы. Например Алишер Усманов. Каждое слово минимум по 3 буквы"
        required
        placeholder='Алишер  Усманов'/>
    </label>
  )
}

function InputPassword() {
  return (
    <label>
      <input
        type='password'
        pattern='[A-Za-zА-Яа-яЁё,0-9]{8,}'//type check here
        title='*Разве это пароль? Минимум 8 символов.'
        required
        placeholder='********'/>
    </label>)
}

function InputEmail() {
  
  return (
    <label>
      <input
        pattern='[A-Za-z]{1,20}[@]{1,}[A-Za-z]{2,10}[.]{1}[a-z]{2,4}'//type check here
        title='*Укажите вашу электронную почту. Например mail@mail.ru'
        type='text'
        required
        placeholder='mail@mail.ru'/>
    </label>)
}

function InputTel() {
  
  return (
    <label>
      <input
        type='tel'
        required
        pattern='+79[0-9]{9}'//type check here
        title="*Введите правильный номер телефона. Например 7-9xx-xxxxxxx"
        placeholder='+7-9xx-xxxxxxx'/>
    </label>)
  
}

function Button() {
  let buttonLabelChecked = true;
  return (
    <button
      type='submit'
    >{buttonLabelChecked ? 'Зарегистрироваться' : 'Заполните форму'
    
    }</button>
  )
}

function Form() {
  return (
    <React.Fragment>
      <InputName/>
      <InputPassword/>
      <InputEmail/>
      <InputTel/>
      <Button/>
    </React.Fragment>
  );
}


ReactDOM.render(
  <Form/>,
  document.querySelector('#form')
);






